//
// Layout Rules
//
// Layout styles can also be divided into major and minor styles based on
// reuse. Fox example, major layout styles can be dedicated for header and/or footer.

html,
body {
    height: 100%;
    margin: 0;
    padding: 0;
    width: 100%;
}

body {
    font-size: $base-font-size;
    line-height: $base-line-height;

    //To prevent the iPhone and iPad from inflating fonts
    @include respond-to(1024, max) {
        -webkit-text-size-adjust: 100%;
    }
}

// ==========================================================================
// Selected text effects
// ==========================================================================
@include selection {
    //type style here
}

// ==========================================================================
// Link styles
// ==========================================================================
a {
    &:hover {

    }
    &:focus {

    }
}

// ==========================================================================
// Clearfix
// ==========================================================================
.clearfix {
    @extend %clearfix;
}